<template>
    <v-toolbar>
        <v-toolbar-items class="hidden-sm-and-down">
            <v-btn flat><router-link to="/"></router-link></v-btn>
            <v-btn flat><router-link to="/users"></router-link></v-btn>
            <v-btn flat><router-link to="/about"> </router-link></v-btn>   
        </v-toolbar-items> 
        <v-spacer></v-spacer>
        <v-toolbar-items v-if="isAuth" class="hidden-sm-and-down">
            <v-btn flat><router-link to="/profile"></router-link></v-btn>
            <v-btn flat><router-link to="/marks"> </router-link></v-btn>
            <v-btn flat><router-link to="/settings"></router-link></v-btn>
        </v-toolbar-items>

        <div v-if="!isAuth" >
            <v-btn color="primary"><router-link class="white--text" to="/signin"></router-link></v-btn>
            <v-btn color="success"><router-link class="white--text" to="/signup"></router-link></v-btn>
        </div>
        <v-menu v-else offset-y>
            <v-btn icon dark 
                slot="activator"
                color="primary"
            >
                <v-icon>account_circle</v-icon>
            </v-btn>
            
            <v-list>
                <v-list-tile>
                    <v-list-tile-title><button @click="signOut()"></button></v-list-tile-title>
                </v-list-tile>
            </v-list>
        </v-menu>
    </v-toolbar>
</template>

<script>
    import auth from "../modules/auth.js";

    export default {
        data() {
            return {
                isAuth: false,
                test: false,
                user: auth.getUser()
            }
        },
        created() {
            auth.isAuthenticated((isAuth)=> {
                this.isAuth = isAuth
            });
        },
        methods: {
            signOut() {
                auth.signOut();
                this.$router.push("/");
            }
        }
    }

</script>

<style>
.v-toolbar a {
    text-decoration: none;
}


</style>



