<template>
    <v-footer
        height="auto"
        color="primary lighten-1"
    >
    <v-card-text
        primary
        lighten-2
        py-3
        text-xs-center
        xs12
        class="white--text"
    >
        &copy;{{ currentYear }}  <strong>CreepTeam</strong>
    </v-card-text>
    </v-footer>
</template>
<script>
    export default {
        data() {
            return {
                currentYear: new Date().getFullYear()
            }
        }
    }
</script>


