<template>
    <v-container>
        <v-layout justify-center>
            <v-flex md6 lg3>
                <v-card>
                    <v-form v-model="valid">
                        <v-text-field
                            v-model="user.login"
                            :rules="inputRules"
                            label=""
                            required
                        ></v-text-field>
                        <v-text-field
                            v-model="user.password"
                            :append-icon="showPassword ? 'visibility_off' : 'visibility'"
                            :type="showPassword ? 'text' : 'password'"
                            :rules="inputRules"
                            label=""
                            required
                            @click:append="showPassword = !showPassword"
                        ></v-text-field>
                        <v-btn 
                            @click="login()"
                        >
                        
                        </v-btn>
                    </v-form>
                </v-card>
                
                <v-alert
                    :value="error"
                    dismissible
                    color="error"
                    type="error"
                >
                {{ error }}
                </v-alert>
                

            </v-flex>
        </v-layout>
    </v-container>
</template>

<script>

import auth from "../modules/auth.js";

export default {
    data() {
        return {
            error: false,
            user: {
                login: "",
                password: ""
            },
            valid: false,
            inputRules: [
                v => !!v || ' '
            ],
            showPassword: false
        }
    },
    methods: {
        test() {
            console.log(1)
        },
        login() {
            
            console.log(123)

            if(this.user.login === "" || this.user.password === "") {
                this.error = " !";
                return;
            }

            auth.login(this.user, ()=> {
                window.location = "/";
            },
            () => {
                this.error = "  !";
            });
        }
    }
}

</script>

<style>


</style>



