import axios from 'axios';

const authUrl = "http://mark-on-the-map.mcdir.ru/auth.php"; // http://mark-on-the-map.mcdir.ru/auth.php

export default {

    setUser(user) { //         
        localStorage.setItem("user", JSON.stringify(user));

        console.log(localStorage.getItem("user"));
    },
    getUser() {
        if(localStorage.getItem("user") !== null) {
            return JSON.parse(localStorage.getItem("user"));
        }
    },


    register(user, cb) { // 
        axios.post(authUrl, {
            data: {
                requestType: "signUp",
                user: user
            }
        })
        .then(response => {
            console.log(response.data)

            if(response.data) { //         
                // this.setUser(response.data);
                cb();
            }
        })
        .catch(e => {
            this.errors.push(e)
        });
    },

    login(user, res, rej) { // 
        axios.post(authUrl, {
            data: {
                requestType: "login",
                user: user
            }
        })
        .then(response => {
            if(response.data) { //         
                this.setUser(response.data);
                res();
            }
            else {
                rej();
            }
        })
        .catch(e => {
            this.errors.push(e)
        });

    },

    signOut() {

        axios.post(authUrl, {
            data: {
                requestType: "signOut",
                user: this.getUser()
            }
        })
        .then(response => {
            console.log( response.data )

            localStorage.removeItem("user");
            window.location = "/";
        })
        .catch(e => {
            this.errors.push(e)
        });

        
    },

    isAuthenticated(cb) { //   
        let status = false;
        
        axios.post(authUrl, {
            data: {
                requestType: "isAuthenticated",
                user: this.getUser()
            }
        })
        .then(response => {
            console.log( response.data )
            cb(response.data);
        })
        .catch(e => {
            this.errors.push(e)
        });

        return status;
    }

}


