<template>
    <v-container>
        <v-layout justify-center>
            <v-flex md6 lg3>
                <v-card>
                    <v-form>
                        <v-text-field
                            v-model="user.login"
                            :rules="inputRules"
                            label=""
                            required
                        ></v-text-field>
                        <v-text-field
                            v-model="user.email"
                            :rules="inputRules"
                            label="Email"
                            required
                        ></v-text-field>
                        <v-text-field
                            v-model="user.password"
                            :rules="inputRules"
                            :append-icon="showPassword ? 'visibility_off' : 'visibility'"
                            :type="showPassword ? 'text' : 'password'"
                            @click:append="showPassword = !showPassword"
                            label=""
                            required
                        ></v-text-field>
                        <v-text-field
                            v-model="user.repassword"
                            :rules="inputRules"
                            :append-icon="showRepassword ? 'visibility_off' : 'visibility'"
                            :type="showRepassword ? 'text' : 'password'"
                            @click:append="showRepassword = !showRepassword"
                            label=" "
                            required
                        ></v-text-field>
                        <v-text-field
                            v-model="user.name"
                            :rules="inputRules"
                            label=""
                            required
                        ></v-text-field>
                        <v-text-field
                            v-model="user.surname"
                            :rules="inputRules"
                            label=""
                            required
                        ></v-text-field>

                        
                        <v-btn @click="register($event)" type="button"></v-btn>

                        <!--
                        <input type="text" v-model="user.login" placeholder="login" required>
                        <input type="email" v-model="user.email" placeholder="Email" >
                        <input type="password" v-model="user.password" placeholder="">
                        <input type="password" v-model="user.repassword" placeholder=" ">
                        <input type="text" v-model="user.name" placeholder="">
                        <input type="text" v-model="user.surname" placeholder="">
                        -->
                
                    </v-form>
                </v-card>
                <v-alert
                    :value="error"
                    dismissible
                    color="error"
                    type="error"
                    transition="scale-transition"
                >
                    {{ error }}
                </v-alert>

                <v-alert
                    :value="success"
                    dismissible
                    color="success"
                    type="success"
                    transition="scale-transition"
                >
                    <v-layout align-center>
                        {{ success }}
                        <v-btn>
                            <router-link to="/signin"></router-link>
                        </v-btn>
                    </v-layout>

                </v-alert>

            </v-flex>
        </v-layout>
    </v-container>
</template>

<script>

import axios from 'axios';
import auth from "../modules/auth.js";

export default {
    data() {
        return {
            user: {
                login: "",
                email: "",
                password: "",
                repassword: "",
                name: "",
                surname: ""
            },
            inputRules: [
                v => !!v || ' '
            ],
            error: false,
            success: false,

            showPassword: false,
            showRepassword: false
        }
    },
    methods: {
        register(e) {

            if(this.user.login === "" || this.user.email === "" || this.user.password === "" || this.user.repassword === "" || this.user.name === "" || this.user.surname === "" ) {
                this.error = "  ";
                return;
            }

            if(this.user.password !== this.user.repassword) {
               this.error = "  !";
               return;
            }

            auth.register(this.user, ()=> {
                this.success = "  !";
                this.$router.push("/signin");
            });
        }
    }
}
</script>




