import Vue from 'vue';
import App from './App.vue';

import VueRouter from 'vue-router';
Vue.use(VueRouter);

import Vuetify from 'vuetify';
Vue.use(Vuetify);


import auth from './modules/auth.js';


// routes
import Index from "./routes/Index.vue";
import Profile from "./routes/Profile.vue";
import About from "./routes/About.vue";
import Marks from "./routes/Marks.vue";
import Settings from "./routes/Settings.vue";
import Users from "./routes/Users.vue";

import SignUp from "./routes/signUp.vue";
import SignIn from "./routes/signIn.vue";

let router = new VueRouter({
  routes: [
    { path: "/", component: Index },
    { path: "/profile", component: Profile, meta: { requiresAuth: true } },
    { path: "/about", component: About },
    { path: "/marks", component: Marks, meta: { requiresAuth: true }  },
    { path: "/settings", component: Settings, meta: { requiresAuth: true }  },
    { path: "/users", component: Users },

    { path: "/signup", component: SignUp },
    { path: "/signin", component: SignIn }
  ]
});

router.beforeEach((to, from, next) => {

    if (to.matched.some(record => record.meta.requiresAuth)) {
      //    ,   
      // ,   ,          
      auth.isAuthenticated((isAuth) => {
        if (!isAuth) {
          alert("     !")
          next({
            path: '/'
          })
        } else {
          next()
        }
      });
    } else {
      next() //       next()!
    }

 

  
  

});


new Vue({
  el: '#app',
  router: router,
  render: h => h(App)
});



